import React, { useState, useEffect, useContext } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import { TaskContext } from "../context/taskContext/TaskContext";
import { getTasks } from "../context/taskContext/apiCalls";
import moment from "moment";
import { Form, Button, Row, Col } from "react-bootstrap";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
//import { set } from 'mongoose';

function DashboardBody(props) {
  let [projects, setProjects] = useState([]);
  let [projectsData, setProjectsData] = useState([]);
  let [subProjects, setSubProjects] = useState([]);
  let [filterTasks, setFilterTasks] = useState([]);
  let [items, setItems] = useState([]);
  let [allItems, setAllItems] = useState([]);
  let [data, setData] = useState([]);
  let [dailyData, setDailyData] = useState([]);
  const { tasks, dispatch } = useContext(TaskContext);
  let [form, setForm] = useState({});
  let [stat, setStat] = useState({});
  let [projectID, setProjectID] = useState(0);
  let [assignedProjectManager, setAssignedProjectManager] = useState("");
  let [companies, setCompanies] = useState([]);
  let [users, setUsers] = useState([]);
  let [lineChart1, setLineChart1] = useState([]);
  let [lineChart2, setLineChart2] = useState([]);
  let [openDialog, setOpenDialog] = useState(false);

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  let setField = (field, value) => {
    setForm({
      ...form,
      [field]: value,
    });
  };

  const changeProject = (e) => {
    setField("project", e.target.value);
    //         console.log(e.target.value)
    //  console.log(projectsData)
    // console.log(projectsData)
    let t = projectsData.filter((task) => task.project == e.target.value);
    setSubProjects([...new Set(t.map((ele) => ele.subProject))]);
    // console.log(t)
    for (var i = 0; i < projectsData.length; i++) {
      if (
        e.target.value == projectsData[i].project &&
        form.subProject == projectsData[i].subProject
      ) {
        // console.log(projectsData[i])
        setProjectID(projectsData[i].projectID);
        // console.log(projectsData[i].projectID)

        i = projectsData.length;
      } else if (i == projectsData.length - 1) {
        setProjectID("");
      }
    }
  };

  const changeSubProject = (e) => {
    // console.log(filterTasks)
    setField("subProject", e.target.value);
    // console.log(e.target.value)

    for (var i = 0; i < projectsData.length; i++) {
      if (
        form.project == projectsData[i].project &&
        e.target.value == projectsData[i].subProject
      ) {
        setAssignedProjectManager(projectsData[i].assignedProjectManager);
        setProjectID(projectsData[i].projectID);
        // console.log(projectsData[i])

        i = projectsData.length;
      } else if (i == projectsData.length - 1) {
        setProjectID("");
        setAssignedProjectManager("");
      }
    }
  };

  useEffect(() => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/users", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setUsers(
          res.data.users.filter((obj) => obj["userCanAssignForTask"] === true)
        );
        setCompanies(
          res.data.companys.filter((obj) => obj.companyName !== "Mobitel")
        );
        return;
      })
      .catch((err) => {
        console.log(err);
      });
    axiosInstance
      .get("projonline/project", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setForm({});

        setProjects([
          ...new Set(res.data.projTemplates.map((ele) => ele.project)),
        ]);
        setSubProjects([
          ...new Set(res.data.projTemplates.map((ele) => ele.subProject)),
        ]);
        setProjectsData(res.data.projTemplates);
      })
      .catch((err) => {
        console.log(err);
      });
    axiosInstance
      .get("projonline/gettasks", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        // console.log(res.data)
        setItems(
          res.data.tasks.filter((obj) => obj["taskStatus"] != "Withdraw")
        );

        setAllItems(
          res.data.tasks.filter((obj) => obj["taskStatus"] != "Withdraw")
        );
        setStatFields(
          res.data.tasks.filter((obj) => obj["taskStatus"] != "Withdraw")
        );
        setOpenDialog(false);
        //setData(res.data.chartData)
        return;
      })
      .catch((err) => {
        console.log(err);
      });
    setLineChart1([
      {
        name: "No of tasks handed over",
        dataKey: "taskHandedover",
        stroke: "#06011c",
        active: true,
      },
      {
        name: "No of subcon allocated",
        dataKey: "subconAllocated",
        stroke: "#dc3545",
        active: true,
      },
      {
        name: "No of tasks accepted",
        dataKey: "taskAccept",
        stroke: "#17a2bb",
        active: true,
      },
      {
        name: "No of tasks completed",
        dataKey: "taskComplete",
        stroke: "#38a852",
        active: true,
      },
      {
        name: "No of BOQs submitted",
        dataKey: "boqSubmitted",
        stroke: "#6c757d",
        active: true,
      },
      {
        name: "No of BOQs approved",
        dataKey: "boqApproved",
        stroke: "#677c14",
        active: true,
      },
      {
        name: "No of PR raised",
        dataKey: "prRaised",
        stroke: "#d1882b",
        active: true,
      },
    ]);

    setLineChart2([
      {
        name: "Pending Subcon allocations",
        dataKey: "SubconAllocated",
        stroke: "#dc3545",
        active: true,
      },
      {
        name: "Pending Tasks acceptance",
        dataKey: "TaskAccept",
        stroke: "#17a2bb",
        active: true,
      },
      {
        name: "Pending Tasks completion",
        dataKey: "TaskComplete",
        stroke: "#38a852",
        active: true,
      },
      {
        name: "Pending BOQ submissions",
        dataKey: "BoqSubmitted",
        stroke: "#6c757d",
        active: true,
      },
      {
        name: "Pending BOQ verifications",
        dataKey: "BoqVerify",
        stroke: "#677c14",
        active: true,
      },
      {
        name: "Pending PR initiations",
        dataKey: "PrRaised",
        stroke: "#d1882b",
        active: true,
      },
    ]);

    // getChartData()
  }, []);

  useEffect(() => {
    const result = allItems.filter(
      (t) =>
        (form.project ? t.project === form.project : true) &&
        (form.subProject ? t.subProject === form.subProject : true) &&
        (form.subcon ? t.assignedSubcon?.companyName === form.subcon : true) &&
        (form.siteEngineer
          ? t.assignedMobitelOfficer?._id === form.siteEngineer
          : true)
    );
    setItems(result);
  }, [form]);

  useEffect(() => {
    getChartData();
    getDailyChartData();
    setStatFields(items);
  }, [items]);

  const legend1Onclick = (e) => {
    lineChart1.map((c) => {
      if (e.dataKey === c.dataKey) {
        var isActive = c.active;
        c.active = !isActive;
      }
    });
    setLineChart1(lineChart1);
    getChartData();
  };

  const legend2Onclick = (e) => {
    lineChart2.map((c) => {
      // console.log(e.dataKey, " ", c.dataKey);
      if (e.dataKey === c.dataKey) {
        var isActive = c.active;
        c.active = !isActive;
      }
    });
    setLineChart2(lineChart2);
    getDailyChartData();
    // console.log(lineChart2);
  };

  const getChartData = () => {
    var chartData = [];
    var date = new Date();
    date.setDate(date.getDate() - 30);

    for (var i = 0; i <= 30; i++) {
      chartData[i] = {
        date: date.toISOString().split("T")[0],
        taskHandedover: items.filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Task handed over")
              .performedDate.split("T")[0] == date.toISOString().split("T")[0]
        ).length,
        subconAllocated: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "Subcon allocated"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "Subcon allocated")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        taskAccept: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "Task accepted"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "Task accepted")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        taskComplete: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "Task completed"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "Task completed")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        boqSubmitted: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "BOQ submitted"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "BOQ submitted")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        boqApproved: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "BOQ Approved"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "BOQ Approved")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        prRaised: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "PR Raised"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "PR Raised")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
      };

      date.setDate(date.getDate() + 1);
    }
    setData(chartData);
  };

  const getDailyChartData = () => {
    var chartData = [];
    var date = new Date();

    date.setDate(date.getDate() - 30);

    for (var i = 0; i <= 30; i++) {
      var pendingsub = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Task handed over"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .reverse()
              .find((x) => x.taskStatus === "Task handed over")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      //console.log(items.filter((obj) => typeof obj.taskHistory.find(x => x.taskStatus === 'Task handed over') !== 'undefined').filter((obj) => obj.taskHistory.reverse().find(x => (x.taskStatus === 'Task handed over' || x.taskStatus==='Task rejected')).performedDate.split('T')[0] <= date.toISOString().split('T')[0]))
      var SubconAllocated = items.filter((obj) =>
        typeof obj.taskHistory.find(
          (x) => x.taskStatus === "Subcon allocated"
        ) !== "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Subcon allocated")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingacc = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Subcon allocated"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Subcon allocated")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var TaskAccept = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.taskStatus === "Task accepted") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Task accepted")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingcomme = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Task accepted"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Task accepted")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var TaskCommenced = items.filter((obj) =>
        typeof obj.taskHistory.find(
          (x) => x.taskStatus === "Task commenced"
        ) !== "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Task commenced")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingcom = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Task commenced"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Task commenced")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var TaskComplete = items.filter((obj) =>
        typeof obj.taskHistory.find(
          (x) => x.taskStatus === "Task completed"
        ) !== "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Task completed")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingBOQsub = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Task completed"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Task completed")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var BoqSubmitted = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.taskStatus === "BOQ submitted") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "BOQ submitted")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingBOQverify = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "BOQ submitted"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .reverse()
              .find(
                (x) =>
                  x.taskStatus === "BOQ submitted" ||
                  x.taskStatus === "BOQ Verified_0" ||
                  x.taskStatus === "BOQ Verified_1" ||
                  x.taskStatus === "BOQ Verified_2"
              )
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var BoqVerify = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.taskStatus === "BOQ Approved") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "BOQ Approved")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingPRraise = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "BOQ Approved"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "BOQ Approved")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var PrRaised = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.taskStatus === "PR Raised") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "PR Raised")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      chartData[i] = {
        date: date.toISOString().split("T")[0],
        SubconAllocated: pendingsub - SubconAllocated,
        TaskAccept: pendingacc - TaskAccept,
        TaskComplete: pendingcom + pendingcomme - TaskCommenced - TaskComplete,
        BoqSubmitted: pendingBOQsub - BoqSubmitted,
        BoqVerify: pendingBOQverify - BoqVerify,
        PrRaised: pendingPRraise - PrRaised,
        // taskAccept: items.filter((obj) => typeof obj.taskHistory.find(x => x.taskStatus === 'Task accepted') !== 'undefined').filter((obj) => obj.taskHistory.find(x => x.taskStatus === 'Task accepted').performedDate.split('T')[0] == date.toISOString().split('T')[0]).length,
        // taskComplete: items.filter((obj) => typeof obj.taskHistory.find(x => x.taskStatus === 'Task completed') !== 'undefined').filter((obj) => obj.taskHistory.find(x => x.taskStatus === 'Task completed').performedDate.split('T')[0] == date.toISOString().split('T')[0]).length,
        // boqSubmitted: items.filter((obj) => typeof obj.taskHistory.find(x => x.taskStatus === 'BOQ submitted') !== 'undefined').filter((obj) => obj.taskHistory.find(x => x.taskStatus === 'BOQ submitted').performedDate.split('T')[0] == date.toISOString().split('T')[0]).length,
        // boqApproved: items.filter((obj) => typeof obj.taskHistory.find(x => x.taskStatus === 'BOQ approved') !== 'undefined').filter((obj) => obj.taskHistory.find(x => x.taskStatus === 'BOQ approved').performedDate.split('T')[0] == date.toISOString().split('T')[0]).length,
        // prRaised: items.filter((obj) => typeof obj.taskHistory.find(x => x.taskStatus === 'PR Raised') !== 'undefined').filter((obj) => obj.taskHistory.find(x => x.taskStatus === 'PR Raised').performedDate.split('T')[0] == date.toISOString().split('T')[0]).length
      };

      date.setDate(date.getDate() + 1);
    }

    setDailyData(chartData);
  };

  const setStatFields = (items) => {
    let noOfTotalTasks = items.length;
    let noOfPendingSubconAllocations = items.filter(
      (obj) =>
        obj["taskStatus"] === "Task handed over" ||
        obj["taskStatus"] === "Task rejected"
    ).length;

    let noOfSubconAllocated = noOfTotalTasks - noOfPendingSubconAllocations;
    let noOfPendingTaskAcceptance = items.filter(
      (obj) => obj["taskStatus"] === "Subcon allocated"
    ).length;
    let NoOfAcceptedTasks = noOfSubconAllocated - noOfPendingTaskAcceptance;
    let noOfPendingTaskCommencement = items.filter(
      (obj) => obj["taskStatus"] === "Task accepted"
    ).length;
    let NoOfCommencedTasks = NoOfAcceptedTasks - noOfPendingTaskCommencement;
    let NoOfPendingTaskCompletion = items.filter(
      (obj) => obj["taskStatus"] === "Task commenced"
    ).length;
    let noOfWorkCompletion = NoOfCommencedTasks - NoOfPendingTaskCompletion;
    let NoOfPendingBOQSubmissions = items.filter(
      (obj) => obj["taskStatus"] === "Task completed"
    ).length;
    let noOfBoqSubmitted = noOfWorkCompletion - NoOfPendingBOQSubmissions;
    let NoOfPendingBOQVerifications = items.filter(
      (obj) =>
        obj["taskStatus"] === "BOQ submitted" ||
        obj["taskStatus"] === "BOQ Verified_0" ||
        obj["taskStatus"] === "BOQ Verified_1" ||
        obj["taskStatus"] === "BOQ Verified_2"
    ).length;
    let noOfBoqVerified = noOfBoqSubmitted - NoOfPendingBOQVerifications;
    let NoOfPendingPrInititations = items.filter(
      (obj) => obj["taskStatus"] === "BOQ Approved"
    ).length;
    let noOfPrRaised = noOfBoqVerified - NoOfPendingPrInititations;

    setStat({
      noOfTotalTasks: noOfTotalTasks,
      noOfPendingSubconAllocations: noOfPendingSubconAllocations,
      noOfSubconAllocated: noOfSubconAllocated,
      noOfPendingTaskAcceptance: noOfPendingTaskAcceptance,
      NoOfAcceptedTasks: NoOfAcceptedTasks,
      noOfPendingTaskCommencement: noOfPendingTaskCommencement,
      NoOfCommencedTasks: NoOfCommencedTasks,
      NoOfPendingTaskCompletion: NoOfPendingTaskCompletion,
      noOfWorkCompletion: noOfWorkCompletion,
      NoOfPendingBOQSubmissions: NoOfPendingBOQSubmissions,
      noOfBoqSubmitted: noOfBoqSubmitted,
      NoOfPendingBOQVerifications: NoOfPendingBOQVerifications,
      noOfBoqVerified: noOfBoqVerified,
      NoOfPendingPrInititations: NoOfPendingPrInititations,
      noOfPrRaised: noOfPrRaised,
    });
  };

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        {/* Content Header (Page header) */}
        <div className="content-header">
          <Row className="mb-3">
            <Form.Group as={Col}>
              <Form.Control
                onChange={changeProject}
                value={form.project}
                as="select"
                defaultValue="All Projects"
              >
                <option value="">All Projects</option>
                {projects.map((opt) => (
                  <option value={opt}>{opt}</option>
                ))}
              </Form.Control>
            </Form.Group>

            <Form.Group as={Col}>
              <Form.Control
                onChange={changeSubProject}
                disabled={form.project ? false : true}
                value={form.subProject}
                as="select"
                defaultValue="All Subproects"
              >
                <option value="">All Subprojects</option>
                {subProjects.map((opt) => (
                  <option value={opt}>{opt}</option>
                ))}
              </Form.Control>
            </Form.Group>
            <Form.Group as={Col}>
              <Form.Control
                onChange={(e) => setField("subcon", e.target.value)}
                value={form.subcon}
                as="select"
                defaultValue="All Subcons"
              >
                <option value="">All Subcons</option>
                {companies.map((opt) => (
                  <option value={opt.companyName}>{opt.companyName}</option>
                ))}
              </Form.Control>
            </Form.Group>
            <Form.Group as={Col}>
              <Form.Control
                onChange={(e) => setField("siteEngineer", e.target.value)}
                value={form.siteEngineer}
                as="select"
                defaultValue="All Site Engineers"
              >
                <option value="">All Site Engineers</option>
                {users.map((opt) => (
                  <option value={opt._id}>{opt.name}</option>
                ))}
              </Form.Control>
            </Form.Group>
          </Row>
        </div>
        {/* /.content-header */}
        {/* Main content */}
        <section className="content">
          <div className="container-fluid">
            {/* Small boxes (Stat box) */}
            <div className="row ">
              <div className="col-md-8 row d-flex align-items-end">
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#dc3545" }}
                    >
                      <i className="fas fa-users" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Subcon Allocations
                      </span>
                      <span className="info-box-number text-white">
                        {stat.noOfPendingSubconAllocations}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* /.info-box */}
                </div>

                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#17a2bb" }}
                    >
                      <i className="fas fa-people-carry" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task Acceptance
                      </span>
                      <span className="info-box-number text-white">
                        {stat.noOfPendingTaskAcceptance}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* /.info-box */}
                </div>

                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#38a852" }}
                    >
                      <i className="fas fa-check-double" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task Completion
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingTaskCompletion +
                          stat.noOfPendingTaskCommencement}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* /.info-box */}
                </div>

                {/* /.col */}
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box mb-3 "
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#6c757d" }}
                    >
                      <i className="fas fa-file-import" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending BoQ Submissions
                      </span>
                      <span className="info-box-number text-white ">
                        {stat.NoOfPendingBOQSubmissions}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                  {/* /.info-box */}
                </div>
                {/* /.col */}
                {/* fix for small devices only */}
                <div className="clearfix hidden-md-up" />
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box mb-3 "
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#677c14" }}
                    >
                      <i className="fas fa-file-signature" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending BoQ verifications
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingBOQVerifications}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                  {/* /.info-box */}
                </div>
                {/* /.col */}
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box mb-3"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#d1882b" }}
                    >
                      <i className="fas fa-clipboard-list" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending PR Initiations
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingPrInititations}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                  {/* /.info-box */}
                </div>
                {/* /.col */}
              </div>

              <div class="col-md-4">
                <div class="progress-group">
                  No of subcon allocated
                  <span class="float-right">
                    <b> {stat.noOfSubconAllocated}</b>/ {stat.noOfTotalTasks}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#dc3545",
                        width: `calc(100% *${stat.noOfSubconAllocated}/${stat.noOfTotalTasks})`,
                      }}
                    ></div>
                  </div>
                </div>

                <div class="progress-group">
                  No of accepted tasks
                  <span class="float-right">
                    <b> {stat.NoOfAcceptedTasks}</b>/ {stat.noOfSubconAllocated}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#17a2bb",
                        width: `calc(100% *${stat.NoOfAcceptedTasks}/${stat.noOfSubconAllocated})`,
                      }}
                    ></div>
                  </div>
                </div>

                <div class="progress-group">
                  No of work completion
                  <span class="float-right">
                    <b> {stat.noOfWorkCompletion}</b>/ {stat.NoOfAcceptedTasks}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#38a852",
                        width: `calc(100% *${stat.noOfWorkCompletion}/${stat.NoOfAcceptedTasks})`,
                      }}
                    ></div>
                  </div>
                </div>

                <div class="progress-group">
                  No of BOQ submitted
                  <span class="float-right">
                    <b> {stat.noOfBoqSubmitted}</b>/ {stat.noOfWorkCompletion}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#6c757d",
                        width: `calc(100% *${stat.noOfBoqSubmitted}/${stat.noOfWorkCompletion})`,
                      }}
                    ></div>
                  </div>
                </div>

                <div class="progress-group">
                  No of BOQs approved
                  <span class="float-right">
                    <b> {stat.noOfBoqVerified}</b>/ {stat.noOfBoqSubmitted}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#677c14",
                        width: `calc(100% *${stat.noOfBoqVerified}/${stat.noOfBoqSubmitted})`,
                      }}
                    ></div>
                  </div>
                </div>

                <div class="progress-group">
                  No of PR raised
                  <span class="float-right">
                    <b> {stat.noOfPrRaised}</b>/ {stat.noOfBoqVerified}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#d1882b",
                        width: `calc(100% *${stat.noOfPrRaised}/${stat.noOfBoqVerified})`,
                      }}
                    ></div>
                  </div>
                </div>
              </div>
            </div>

            {/* /.row */}
            {/* Main row */}
            <div className="row">
              {/* Left col */}
              <section className="col-lg-12 connectedSortable">
                {/* Custom tabs (Charts with tabs)*/}
                <div className="card bg-light" style={{ height: "32rem" }}>
                  <div className="card-header">
                    <h3 className="card-title">
                      <i className="fas fa-chart-pie mr-1" />
                      Daily stats
                    </h3>
                  </div>
                  {/* /.card-header */}
                  <div className="card-body ">
                    <ResponsiveContainer>
                      <LineChart
                        data={data}
                        margin={{
                          top: 5,
                          right: 25,
                          left: 0,
                          bottom: 5,
                        }}
                      >
                        <CartesianGrid strokeDasharray="1 1" />
                        <XAxis dataKey="date" />
                        <YAxis />
                        <Tooltip />
                        <Legend onClick={legend1Onclick} />
                        {lineChart1.map((c) => (
                          <Line
                            type="monotone"
                            name={c.name}
                            dataKey={c.dataKey}
                            stroke={c.stroke}
                            strokeWidth={3}
                            hide={!c.active}
                          />
                        ))}
                      </LineChart>
                    </ResponsiveContainer>
                  </div>
                  {/* /.card-body */}
                </div>
                {/* /.card */}
              </section>
              {/* /.Left col */}
            </div>
            {/* /.row (main row) */}

            {/* /.row */}
            {/* Main row */}
            <div className="row">
              {/* Left col */}
              <section className="col-lg-12 connectedSortable">
                {/* Custom tabs (Charts with tabs)*/}
                <div className="card bg-light" style={{ height: "32rem" }}>
                  <div className="card-header">
                    <h3 className="card-title">
                      <i className="fas fa-chart-pie mr-1" />
                      Pending Stats
                    </h3>
                  </div>
                  {/* /.card-header */}
                  <div className="card-body ">
                    <ResponsiveContainer>
                      <LineChart
                        data={dailyData}
                        margin={{
                          top: 5,
                          right: 25,
                          left: 0,
                          bottom: 5,
                        }}
                      >
                        <CartesianGrid strokeDasharray="1 1" />
                        <XAxis dataKey="date" />
                        <YAxis />
                        <Tooltip />
                        <Legend onClick={legend2Onclick} />
                        {lineChart2.map((c) => (
                          <Line
                            type="monotone"
                            name={c.name}
                            dataKey={c.dataKey}
                            stroke={c.stroke}
                            strokeWidth={3}
                            hide={!c.active}
                          />
                        ))}
                      </LineChart>
                    </ResponsiveContainer>
                  </div>
                  {/* /.card-body */}
                </div>
                {/* /.card */}
              </section>
              {/* /.Left col */}
            </div>
            {/* /.row (main row) */}
          </div>
          {/* /.container-fluid */}
        </section>
        {/* /.content */}
      </div>
      <Dialog
        open={openDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(DashboardBody);
